@mixin 함수() {
  background-color: #eee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}
.my-alert{
  @include 함수();
}
.my-alert2{
  display: none;
}

.my-alert p{
  margin-bottom: 0;
}